.main {
  flex: 1 0 auto;
}

@include viewport--md {
  .main--js {
    display: none;
  }
}

.main__catalog {
  display: grid;
  grid-template-columns: 250px 1fr;
  column-gap: 50px;
  grid-template-areas: "filter catalog";

  @include viewport--md {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
}
