.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;

  display: flex;
  flex-direction: column;
  overflow-y: auto;

  background-color: rgba($color-default-black, 0.6);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  pointer-events: none;

  transition: opacity $modal-transition-settings,
    transform $modal-transition-settings,
    visibility $modal-transition-settings;

  -ms-overflow-style: none;
  scrollbar-width: none;

  &::-webkit-scrollbar {
    display: none;
  }

  &__wrapper {
    position: relative;

    display: flex;
    flex-shrink: 0;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 100%;
    padding: 30px 40px;

    @include viewport--sm {
      padding: 10px 16px;
    }
  }

  &__overlay {
    position: absolute;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    cursor: pointer;
  }

  &__content {
    position: relative;

    width: 500px;
    padding: 47px 35px;

    background-color: $color-default-white;

    @include viewport--sm {
      width: 100%;
      padding: 40px 15px;
    }
  }

  &__close-btn {
    position: absolute;
    top: 19px;
    right: 19px;

    @include viewport--sm {
      top: 15px;
      right: 15px;
    }
  }

  &__close-btn:hover svg {
    fill: $color-medium-wood;
  }

  &--responsive {
    .modal__content {
      width: 100%;
    }
  }

  &--fit-content .modal__content {
    width: auto;

    @include viewport--sm {
      width: 100%;
    }
  }

  &--no-scale {
    transform: none;
  }

  &--preload {
    display: none;
  }

  &--active {
    transform: scale(1);
    visibility: visible;
    opacity: 1;
    pointer-events: auto;
  }
}

.modal--login {
  .modal__content {
    padding: 0;
    width: 430px;

    @include viewport--sm {
      width: 270px;
    }
  }

  .modal-add {
    display: none;
  }
}

.modal--add {
  .login {
    display: none;
  }
}
